.price {
  margin-bottom: 12.1875vw;
}

.price__wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 3.17708vw;
}

.price__column {
  box-sizing: border-box;
  position: relative;
  padding: 2.0833vw 2.6041vw;

  max-width: 507px;
  // max-height: 700px;
  width: 26.40625vw;
  // height: 36.45833vw;

  box-shadow: 0 0 29px 3px rgba(2, 7, 32, 0.04);
  border-radius: 19px;
  background-color: #ffffff;
}

.price__title {
  margin-bottom: 2.91666vw;
  font-size: 1.5625vw;
  text-align: center;
}

.price__icon-img {
  max-width: 26px;
  max-height: 26px;
  width: 1.3541vw;
  height: 1.3541vw;
  padding-right: 0.781vw;

  &--narco {
    max-width: 30px;
    max-height: 29px;
    width: 1.56vw;
    height: 1.5104vw;
  }

  &--alco {
  }

  &--other {
    max-height: 30px;
    height: 1.56vw;;
    width: auto;
  }
}

.price__table {
  width: 100%;
}

.price__table td {
  font-size: 0.989vw;
  padding-bottom: 1.510vw;
}

.price__table td:first-child {
  width: 68%;
  text-align: left;
  color: #747474;
}

.price__table td:last-child {
  text-align: right;
  color: #2f2f2f;
  font-weight: 700;
}

.price__link {
  position: absolute;
  bottom: -4.4%;
  left: 50%;

  transform: translateX(-50%);

  border-radius: 28px;
}

@media  (max-width: @mobile-width) {
  .price {
    margin-bottom: 23.733vw;
  }

  .price__wrapper {
    flex-direction: column;
    margin-top: 11.977vw;
  }

  .price__column {
    max-width: none;
    width: 100%;
    margin-bottom: 19.333vw;
    padding-top: 7.783vw;
    .padding-inline(3.904vw);
    padding-bottom: 12.083vw;

    text-align: center;
  }

  .price__column:last-child {
    margin-bottom: 0;
  }

  .price__title {
    position: relative;
    display: inline-block;
    margin-bottom: 7.317vw;
    padding-left: @m82px;

    font-size: @m35px;
  }

  .price__icon-img {
    position: absolute;
    top: -@m7px;
    left: -@m4px;

    .mn();
    width: @m60px;
    height: @m60px;
    padding-right: 0.781vw;

    // &--narco {
    //   top: -@m7px;
    // }

    &--alco {
      top: -@m15px;
    }

    &--other {
      top: -@m13px;
    }
  }

  .price__table td {
    font-size: @m30px;
    padding-bottom: 4.51vw;
  }

  .price__link {
    bottom: -3.6%;
    width: 44vw;
    border-radius: 38px;
  }
}

@media (min-width: @max-desktop-width) {
  .price {
    margin-bottom: 234px;
  }

  .price__wrapper {
    margin-top: 61px;
  }

  .price__column {
    padding: 40px 50px;
  }

  .price__title {
    margin-bottom: 56px;
    font-size: 30px;
  }

  .price__icon-img {
    padding-right: 15px;
  }

  .price__table td {
    font-size: 19px;
    padding-bottom: 29px;
  }
}
